@import "../_try-mixins.less";
@import "../_try-variables.less";

@media screen and (max-width:380px) {
	.col-lh{
		overflow: visible!important;
		line-height: 0.64rem!important;
	}
}

.fn-@{try-ns}-list-item{
	padding-top: 0.2rem;

	.list-row{
		position: relative;
		background: #fff;
		padding-bottom: 0.36rem;

		i{
			font-style: normal;
		};
	}
	.try-icon{
		.absolute(-0.2rem,_,_,0.2rem);
		font-size: 0.48rem;
		z-index: 3;
		.size(1.4rem,1.2rem);
		color:#fff;
		text-align: center;
		padding-right: 0.16rem;
		line-height: 0.96rem;

		&-post{
			background: data-uri('@{try-image-path}/icons-post.png') 0 0 no-repeat;
			background-size: 100% 100%;
		}
		&-free{
			background: data-uri('@{try-image-path}/icons-free.png') 0 0 no-repeat;
			background-size: 100% 100%;
		}
	}

	.tips{
			.absolute(4%,_,_,4%);
			.size(1.2rem);
			border-radius: 1.2rem;
			font-size: 0.48rem;
			background: rgba(219,59,76,80);
			color:#fff;
			text-align: center;
			line-height: 2.5;
			z-index: 2;
		}

	.title{
			font-size: @font-size-base;
			height: 1.84rem;
			line-height: 0.8rem;
			padding: 0.24rem 0.36rem 0rem 0rem;
			margin-bottom: 0.56rem;
			overflow: hidden;
			border-top: 1px solid #ccc;

			&-nb{
				border-top: none;
			}
		}

	.col-pd{
		font-size: @font-size-sm;
		color: #acacac;
	}

	.col-pl{
		line-height: 1rem;
	}

	.col-lh{
		line-height: 1rem;
		height: 1rem;
		overflow: hidden;

	}

	.col-pr{
		padding-right: 0.36rem;
		line-height: 1rem;
	}

	.col-l-t{
		text-decoration: line-through;
	}

	.btn{
			color: #fff;
			background: @try-theme-color;
			padding: 0rem 0.24rem;
			display: inline-block;
			vertical-align: top;
			border-radius: 0.16rem;
		}

		.col-ht{
			color: @ht-color;
		}

		.col-green{
			color: @try-theme-color;
		}

		.col-blue{
			color: #0066CC;
		}

		.col-price{
			color: #999;
		}
}

