@import "../_try-mixins.less";
@import "../_try-variables.less";

.fn-@{try-ns}-list-state{
	background: #fff;
	overflow: hidden;

	.list-state{
		>a{
			font-size: @font-size-sm;
			padding: 0.4rem;
			width: 33.3%;
			float: left;
			text-align: center;
			border-bottom:2px solid transparent;

			&:first-child{
				color: @try-theme-color;
				border-bottom: 2px solid @try-theme-color;
			}
		}
	};

	.list-status{
		>a{
			width: 50%;
			float: left;
			padding: 0.4rem;
			text-align: center;

			>span{
				line-height: 0.64rem;
				vertical-align: top;
			};

			>i{
				display: inline-block;
				vertical-align: top;

				>em{
					display: block;
					line-height: 0.32rem;

					&:nth-child(even){
						color: #999;
					}
				}
			}
		};
	}
}
